.navbar1 {
    /* border: 1px solid red; */
    width: 50%;
    display: flex;
    justify-content: space-between;
    margin-left: 600px;
}

.navbar1 a {
    text-decoration: none;
    font-size: 24px;
}

.navbar1 a:hover {
    color: red;
}

.navContainer {
    width: 100%;
    height: 80px;
    margin: auto;
    background-color:rgb(0, 0, 0);
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.sec1, .sec2, .sec3, .sec4, .sec5 {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 300px;
    color: rgb(17, 17, 128);
}